import React from 'react'
import Lottieplayer from '../commanComponent/Lottieplayer'
import backup from '../../../src/images/Backup.json'
import { CustomButton } from '../globalComponents'

const ApproveRejectModal = ({ titile, desc, onApproveReject, isShow, isLoading, btnTextLeft,btnTextRight}) => {
    return (
        <>
            <div class="modal fade attendanceViewodal" id="sureMdl" style={{ display: isShow ? "unset !important" : "none !important", opacity: isShow ? "unset" : "0" }}>
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content rounded-1 border-0">
                        <div class="modal-body ">
                            <div class="text-center suremodalCon">
                                <div class="d-flex justify-content-center">
                                    <Lottieplayer lottieUrl={backup} Lottiestyle={{ width: "90px", height: "90px" }} />
                                </div>
                                <h6 class="fw-semibold mb-2">{titile ? titile : "Are You Sure?"}</h6>
                                <p class="mb-5 px-4">{desc ? desc : ""}
                                </p>
                                <div class="d-flex justify-content-center gap-3">
                                    <CustomButton
                                        classNames={"btn btn-danger"}
                                        isLoading={isLoading}
                                        text={btnTextLeft || "Reject"}
                                        handleClick={() => onApproveReject(false)}
                                    />
                                    <CustomButton
                                        classNames={"btn btn-success"}
                                        isLoading={isLoading}
                                        text={btnTextRight || "Confirm"}
                                        handleClick={() => onApproveReject(true)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ApproveRejectModal
