import React, { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { CreateMrf, MrfProceses, CompleteMrfSteps, MrfTable, MrfSubmissionTable, MrfApprovalTable } from "../components/hiring/mrf"
import { mrfActions } from '../core/resource/actions'
import { pagination } from '../utils/Constants.config'
import { CustomButton } from '../components/globalComponents'
import MrfApprovalModal from '../components/modal/hiring/MrfApprovalModal'
import ApproveRejectModal from '../components/modal/ApproveRejectModal'
import MrfSubmissionView from '../components/modal/hiring/MrfSubmissionView'
const Mrf = () => {
    const dispatch = useDispatch()
    const {
        getAllUserMrfPending,
        getAllUserMrfRes,
        getAllUserMrfError,

        getMrfDashboardAndHierarchyPending,
        getMrfDashboardAndHierarchyRes,
        getMrfDashboardAndHierarchyError,

        getMrfForApprovalDataPending,
        getMrfForApprovalDataRes,
        getMrfForApprovalDataError,

        approveRejectMrfPending,
        approveRejectMrfRes,
        approveRejectMrfError,

        getMrfDataByIdError,
        getMrfDataByIdRes,
        getMrfDataByIdPending,


    } = useSelector(state => state.mrf)

    const [allMrfListData, setAllMrfListData] = useState('')
    const [currentPage, setCurrentPage] = useState(1);
    const [approvalCurrPage, setApprovalCurrPage] = useState(1);
    const [submissionType, setSubmissionType] = useState("submit")
    const [approvalType, setApprovalType] = useState("pending")
    const [mrfDashboardAndHierarchyData, setMrfDashboardAndHierarchyData] = useState()
    const [mrfApprovalData, setMrfApprovalData] = useState([])
    const approvalCalling = useRef(false);
    const [showApproveRejectMrfModal, setShowApproveRejectMrfModal] = useState(false)
    const [showSubmissionApproveRejectModal, setShowSubmissionApproveRejectModal] = useState(false)
    const [mrfId, setMrfId] = useState('')
    const [appliedFor, setAppliedFor] = useState('')
    const [approveRejectComment, setApproveRejectComment] = useState('')
    const [submissionModalComment, setSubmissionModalComment] = useState('')
    const [mrfSubmissionId, setMrfSubmissionId] = useState('')
    const [showSubmitionViewModal, setShowSubmitionViewModal] = useState(false)
    const [mrfByIdData,setMrfByIdData]=useState([])
    useEffect(() => {
        getMrfDashboardAndHierarchyData()
    }, [])

    useEffect(() => {
        getAllUserMrfData()
    }, [currentPage, submissionType])

    const onApproveRejectMrfSubmission = (type) => {
        if (type) {
            const params = {
                query_type: "submit",
                _id: mrfSubmissionId
            }
            dispatch(mrfActions.submitDraftMrfRequest(params))
            setShowSubmissionApproveRejectModal(!showSubmissionApproveRejectModal)
        }
        else {
            setShowSubmissionApproveRejectModal(!showSubmissionApproveRejectModal)
        }

    }





    const getAllUserMrfData = (subType) => {
        const params = {
            query_type: "user",
            type: subType || submissionType,
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(mrfActions.getAllUserMrfRequest(params))
    }

    useEffect(() => {
        if (!getAllUserMrfPending) {
            if (getAllUserMrfError) {
                return;
            }
            if (getAllUserMrfRes) {
                if (getAllUserMrfRes?.data?.code === 200) {
                    const getAllUserMrfData = getAllUserMrfRes?.data?.data?.data
                    setAllMrfListData(getAllUserMrfData)

                }
            }
        }
    }, [
        getAllUserMrfRes,
        getAllUserMrfError,
        getAllUserMrfPending
    ]);

    // get Mrf Dashboard And Hierarchy Pending api call
    const getMrfDashboardAndHierarchyData = () => {
        dispatch(mrfActions.getMrfDashboardAndHierarchyRequest())
    }

    useEffect(() => {
        if (!getMrfDashboardAndHierarchyPending) {
            if (getMrfDashboardAndHierarchyError) {
                return;
            }
            if (getMrfDashboardAndHierarchyRes) {
                if (getMrfDashboardAndHierarchyRes?.data?.code === 200) {
                    const mrfDashboardAndHierarchyTempData = getMrfDashboardAndHierarchyRes?.data?.data
                    setMrfDashboardAndHierarchyData(mrfDashboardAndHierarchyTempData)
                }
            }
        }
    }, [
        getMrfDashboardAndHierarchyRes,
        getMrfDashboardAndHierarchyError,
        getMrfDashboardAndHierarchyPending
    ]);

    //  get Mrf Approval Data api call
    useEffect(() => {
        if (approvalCalling.current) {
            getMrfApprovalData()
        } else {
            approvalCalling.current = true;
        }
    }, [approvalCurrPage, approvalType])


    const getMrfApprovalData = (actionType, currPage) => {
        const params = {
            action_type: actionType || approvalType,
            page: approvalCurrPage,
            limit:pagination.limit,
        }
        dispatch(mrfActions.getMrfForApprovalDataRequest(params))
    }


    useEffect(() => {
        if (!getMrfForApprovalDataPending) {
            if (getMrfForApprovalDataError) {
                return;
            }
            if (getMrfForApprovalDataRes) {
                if (getMrfForApprovalDataRes?.data?.code === 200) {
                    const getMrfForApprovalTempData = getMrfForApprovalDataRes?.data?.data?.data
                    setMrfApprovalData(getMrfForApprovalTempData)

                }
            }
        }
    }, [
        getMrfForApprovalDataRes,
        getMrfForApprovalDataError,
        getMrfForApprovalDataPending
    ]);

    const onTabChange = (tbType) => {
        if (tbType === "mrfApprovalTb") {
            getMrfApprovalData()
        } else {
            getAllUserMrfData()
        }

    }
    // approve reject mrf
    const onApproveRejectMrf = (type) => {
        const params = {
            _id: mrfId,
            action_type: type,
            comment: approveRejectComment
        }
        dispatch(mrfActions?.approveRejectMrfRequest(params))
        setShowApproveRejectMrfModal(!showApproveRejectMrfModal)

    }

    const onHandleApproveReject = (id, type) => {
        setMrfId(id)
        setAppliedFor(type)
        setShowApproveRejectMrfModal(!showApproveRejectMrfModal)
    }
    // approve reject submission modal
    const onHandleSubmissionApproveReject = (id) => {
        setMrfSubmissionId(id)
        setShowSubmissionApproveRejectModal(!showSubmissionApproveRejectModal)
    }



    useEffect(() => {
        if (!approveRejectMrfPending) {
            if (approveRejectMrfError) {
                return;
            }
            if (approveRejectMrfRes) {
                if (approveRejectMrfRes?.data?.code === 200) {
                    // dispatch(mrfActions.handleClearMrfStore('HANDLE_CLEAR_APPROVE_REJECT_MRF_STORE'))
                    setMrfId('')
                    setAppliedFor('')
                }
            }
        }
    }, [
        approveRejectMrfRes,
        approveRejectMrfError,
        approveRejectMrfPending
    ]);

    const handleSubmissionViewModal = (id) => {
        if (id) {
            getMrfDataById(id)
            setShowSubmitionViewModal(!showSubmitionViewModal)
        }
        setShowSubmitionViewModal(!showSubmitionViewModal)
        dispatch(mrfActions.handleClearMrfStore('HANDLE_CLEAR_GET_MRF_DATA_BY_ID_STORE'))

    }

    const getMrfDataById = (id) => {

        const params = {
            _id: id
        }
        dispatch(mrfActions.getMrfDataByIdRequest(params))
    }

    useEffect(() => {
        if (!getMrfDataByIdPending) {
            if (getMrfDataByIdError) {
                return;
            }
            if (getMrfDataByIdRes) {
                if (getMrfDataByIdRes?.data?.code === 200) {
                    const getMrfDataById = getMrfDataByIdRes?.data?.data?.data
                    setMrfByIdData(getMrfDataById)

                }
            }
        }
    }, [
        getMrfDataByIdPending,
        getMrfDataByIdError,
        getMrfDataByIdRes
    ]);



    return (
        <>

            <MrfProceses mrfDashboardAndHierarchyData={mrfDashboardAndHierarchyData} />
            <CreateMrf mrfDashboardAndHierarchyData={mrfDashboardAndHierarchyData}/>

            <div class="cardBox myRequest mb-3">
                <div class="cardHead">
                    <img src="images/myrequest-icon.svg" alt="" width="35" />
                    <h3 class="ms-2">MRF Status</h3>
                </div>
                <ul class="nav nav-tabs leaveTabs mb-4">
                    <li class="nav-item">
                        {/* <CustomButton
                        text={"MRF Submission"}
                        class={`nav-link active`}
                        /> */}
                        <button
                            class="nav-link active"
                            data-bs-toggle="tab"
                            data-bs-target="#mrfSubmissionTb"
                            type="button"
                            onClick={() => onTabChange("mrfSubmissionTb")}
                        >
                            MRF Submission
                        </button>
                    </li>
                    <li class="nav-item">
                        <button
                            class="nav-link"
                            data-bs-toggle="tab"
                            data-bs-target="#mrfApprovalTb"
                            type="button"
                            onClick={() => onTabChange("mrfApprovalTb")}
                        >
                            MRF Approval
                        </button>
                    </li>
                </ul>
                <div class="tab-content">
                    <MrfSubmissionTable
                        allMrfListData={allMrfListData}
                        isLoading={getAllUserMrfPending}
                        currentPage={currentPage}
                        setCurrentPage={setCurrentPage}
                        setSubmissionType={setSubmissionType}
                        submissionType={submissionType}
                        getAllUserMrfData={getAllUserMrfData}
                        onHandleSubmissionApproveReject={onHandleSubmissionApproveReject}
                        handleSubmissionViewModal={handleSubmissionViewModal}

                    />

                    <MrfApprovalTable
                        mrfApprovalData={mrfApprovalData}
                        isLoading={getMrfForApprovalDataPending}
                        currentPage={approvalCurrPage}
                        setApprovalCurrPage={setApprovalCurrPage}
                        setApprovalType={setApprovalType}
                        approvalType={approvalType}
                        getAllUserMrfData={getAllUserMrfData}
                        onHandleApproveReject={onHandleApproveReject}
                        handleSubmissionViewModal={handleSubmissionViewModal}

                    />

                    {showApproveRejectMrfModal && <MrfApprovalModal
                        titile={'MRF Approval'}
                        onApproveReject={onApproveRejectMrf}
                        isShow={showApproveRejectMrfModal}
                        isLoading={approveRejectMrfPending}
                        id={mrfId}
                        appliedFor={appliedFor}
                        setComment={setApproveRejectComment}
                        comment={approveRejectComment}

                    />}

                    {showSubmissionApproveRejectModal && (
                        <ApproveRejectModal
                            onApproveReject={onApproveRejectMrfSubmission}
                            isShow={showSubmissionApproveRejectModal}
                            desc={"You want to submit mrf "}
                            // isLoading={approveRejectMrfPending}
                            btnTextLeft={"Cancel"}
                            btnTextRight={"Submit"}
                        />)
                    }
                </div>
            </div>
            <MrfSubmissionView
                handleSubmissionViewModal={handleSubmissionViewModal}
                openModel={showSubmitionViewModal}
                mrfByIdData={mrfByIdData}

            />
            {/* <CompleteMrfSteps /> */}


        </>
    )
}

export default Mrf
