import React from 'react'
import { CustomButton } from '../../globalComponents'

const MrfApprovalModal = (props) => {
    const { titile, onApproveReject, isShow, isLoading, btnTextLeft, btnTextRight,setComment,id,appliedFor,comment } = props
    return (
        <>
            <div class="modal fade correctionAprovel" id="mrfApproval" style={{ display: isShow ? "unset !important" : "none !important", opacity: isShow ? "unset" : "0" }}>
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content rounded-0 border-0">
                        <div class="modal-header border-0">
                            <h1 class="modal-title fs-5 fw-bold text-primary">{titile}</h1>
                            <CustomButton
                                    classNames={"btn-close"}
                                    handleClick={() => onApproveReject(false)}
                                />
                            {/* <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button> */}
                        </div>
                        <div class="modal-body pt-0">
                            <div class="d-flex align-items-center justify-content-between mb-4">
                                {id &&<strong class="fs-7 text-light">MRF ID<span
                                    class="text-primary fw-bolder d-block">{id}</span></strong>}
                               { appliedFor &&<strong class="fs-7 text-light text-end">Applied For<span
                                    class="text-primary fw-bolder d-block">{appliedFor}</span></strong>}
                            </div>
                            <span class="text-primary fw-semibold mb-2 d-block fs-8">Enter Reason</span>
                            <div class="form-group mb-3">
                                <textarea class="form-control" placeholder="Enter Reason" 
                                value= {comment}
                                style={{ height: "70px" }}
                                onChange={(e)=>setComment(e.target.value)}
                                ></textarea>
                            </div>
                            <div class="my-3 d-flex justify-content-center gap-3">
                                <CustomButton
                                    classNames={"btn btn-danger"}
                                    isLoading={isLoading}
                                    text={btnTextLeft || "Reject"}
                                    handleClick={() => onApproveReject("reject")}
                                />
                                <CustomButton
                                    classNames={"btn btn-success"}
                                    isLoading={isLoading}
                                    text={btnTextRight || "Approve"}
                                    handleClick={() => onApproveReject("approve")}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default MrfApprovalModal
