import React, { useMemo } from 'react'
import Image from '../../commanComponent/Image'
import Button from '../../commanComponent/Button'
import MrfSubmissionView from '../../modal/hiring/MrfSubmissionView'
import { Link } from 'react-router-dom'
import MrfApprovalModal from '../../modal/hiring/MrfApprovalModal'
import CustomePagination from '../../commanComponent/CustomePagination'
import CustomTable from '../../reuseableComponents/customTable/CustomTable'
import { CustomButton } from '../../globalComponents'
import moment from 'moment-timezone'

const MrfApprovalTable = (props) => {
    const {
        mrfApprovalData,
        isLoading,
        currentPage,
        setApprovalCurrPage,
        setApprovalType,
        approvalType,
        getAllUserMrfData,
        onHandleApproveReject,
        handleSubmissionViewModal,
    } = props

    const mrfApprovalFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'collection_id', label: 'MRF ID' },
        { key: 'hiring_for_name', label: 'Location' },
        { key: 'department_name', label: 'Department' },
        { key: 'designation_name', label: 'Designation' },
        { key: 'priority_name', label: 'Prority' },
        { key: 'created_by_name', label: 'Created By' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="actionGroup d-flex gap-2">
               
                <span
                 onClick={()=> handleSubmissionViewModal(item?.collection_id)}
                >
                    <Image imgSrc={"images/view-icon.svg"} altText={""} width={32}></Image>
                </span >
                {(item?.action_type === "pending") && <span
                onClick={()=>onHandleApproveReject(item?._id,item?.mrf_type)}
                >
                    <Image imgSrc={"images/nextaction-Icon.svg"} altText={""}></Image>
                </span>}

            </div>
        )
    }), [mrfApprovalData]);


    const handleSetType = (type) => {
        setApprovalType(type)
        setApprovalCurrPage(1)
    }

    return (
        <>
            <div class="tab-pane fade" id="mrfApprovalTb">
                <div class="requestdata">
                    <div class="d-flex align-items-center mb-4">
                        <span class="fw-bold fs-6 text-black">MRF Approval Status</span>
                        <ul class="nav nav-tabs leaveTabs ms-3">
                            <li class="nav-item">

                                <CustomButton
                                    text="Pending"
                                    classNames={`nav-link px-4 ${approvalType === "pending" ? "active" : ""}`}
                                    handleClick={() => handleSetType("pending")}
                                />
                            </li>
                            <li class="nav-item">

                                <CustomButton
                                    text="Approved"
                                    classNames={`nav-link px-4 ${approvalType === "approve" ? "active" : ""}`}
                                    handleClick={() => handleSetType("approve")}
                                />
                            </li>
                            <li class="nav-item">

                                <CustomButton
                                    text="Reject"
                                    classNames={`nav-link px-4 ${approvalType === "reject" ? "active" : ""}`}
                                    handleClick={() => handleSetType("reject")}
                                />

                            </li>
                        </ul>
                    </div>
                    <div class="tab-content">
                        <div class="tab-pane fade active show" id="pendingMrfStatus">
                            <div class="table-responsive tableDiv text-nowrap">
                                <CustomTable
                                    data={mrfApprovalData?.data}
                                    fields={mrfApprovalFields}
                                    isLoading={isLoading}
                                    pagination={true}
                                    currentPage={currentPage}
                                    setCurrentPage={setApprovalCurrPage}
                                    totalPages={mrfApprovalData?.totalPages}
                                    isHeader={false}
                                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                                    scopedSlots={scopedSlots}
                                />
                            </div>
                        </div>

                    </div>
                </div>
            </div>


        </>
    )
}

export default MrfApprovalTable
