import React, { useEffect, useState } from 'react'
import Button from '../../commanComponent/Button'
import NewHiringOffCanvas from '../../modal/hiring/NewHiringOffCanvas'
import ReplacementMrfOffcanvas from '../../modal/hiring/ReplacementMrfOffcanvas'
import Lottie from 'react-lottie-player'
import businesshiring from "../../../images/business-hiring.json"
import { CustomButton } from '../../globalComponents'
import { useNavigate } from 'react-router-dom'

const CreateMrf = (props) => {
    const { mrfDashboardAndHierarchyData } = props
    const [approvalHierarchyData, setApprovalHierarchyData] = useState('')

    useEffect(() => {
        if (mrfDashboardAndHierarchyData) {
            const { approval_hierarchy } = mrfDashboardAndHierarchyData
            setApprovalHierarchyData(approval_hierarchy)
        }
    }, [mrfDashboardAndHierarchyData])

    const Navigate = useNavigate()

    const createMrfProcess = (type) => {
        Navigate(`/mrf-process-step/${type}`)
    }
    return (
        <>
            <div class="cardBox mb-3 createNewMrf newMrfProcess ">
                <div class="row align-items-center">
                    <div class="col-md-9 col-xl-10">
                        <div class="row align-items-center">
                            <div class="col-md-3">
                                <Lottie
                                    loop
                                    animationData={businesshiring}
                                    background="transparent"
                                    speed="1"
                                    play
                                    style={{ width: "100%", height: "124px" }}
                                    autoplay></Lottie>

                            </div>
                            <div class="col-md-9">
                                <div className="cardHead">
                                    <h3> <span>Approval Hierarchy</span></h3>
                                    <ul className="tackList apphierarchy mt-5 pt-2 pe-4 ">

                                        {approvalHierarchyData?.length > 0 &&
                                            approvalHierarchyData?.map(item => <li className='text-capitalize'>{item}</li>)}
                                        <li>HR Recruiter</li>

                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-3 col-xl-2">
                        <CustomButton
                            classNames="btn btn-outline-primary w-100 mb-2"
                            text="New Hiring"
                            handleClick={() => createMrfProcess("vacancy")}
                        />
                        <CustomButton
                            classNames="btn btn-primary gradientBg w-100"
                            text="Replacement"
                            handleClick={() => createMrfProcess("replacement")}
                        />
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateMrf
