import React, { useMemo } from 'react'
import { CustomButton } from '../../globalComponents';
import CustomTable from '../../reuseableComponents/customTable/CustomTable';
import moment from 'moment-timezone'

import Image from '../../commanComponent/Image';


const MrfSubmissionTable = (props) => {
    const {
        allMrfListData,
        isLoading,
        currentPage,
        setCurrentPage,
        setSubmissionType,
        submissionType,
        getAllUserMrfData,
        onHandleSubmissionApproveReject,
        handleSubmissionViewModal

    } = props
  

    const mrfSubmittedFields = [
        { key: 'sn', label: 'S.N' },
        { key: '_id', label: 'MRF ID' },
        { key: 'hiring_for_name', label: 'Location' },
        { key: 'type', label: 'Applied For' },
        { key: 'action_by', label: 'Approval To' },
        { key: 'approval_status_name', label: 'Approval Status' },
        { key: 'mrf_status', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

   
    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="actionGroup d-flex gap-2">
              
                <span onClick={() => handleSubmissionViewModal(item?._id)}>                   
                       <Image imgSrc={"images/view-icon.svg"} altText={""} width={32}></Image>
                </span>
                {!(item?.is_submitted ) && <span
                    onClick={() => onHandleSubmissionApproveReject(item?._id)}
                >
                    <Image imgSrc={"images/nextaction-Icon.svg"} altText={""}></Image>
                </span>}
            </div>
        )
    }), [allMrfListData,]);
    const handleSetType = (type) => {
        setSubmissionType(type)
        setCurrentPage(1)
    }

    return (
        <div class="tab-pane fade active show" id="mrfSubmissionTb">
            <div class="requestdata">
                <div class="d-flex align-items-center mb-4">
                    <span class="fw-bold fs-6 text-black">MRF Submission Status</span>
                    <ul class="nav nav-tabs leaveTabs ms-3">
                        <li class="nav-item">
                            <CustomButton
                                text="Submitted"
                                classNames={`nav-link px-4 ${submissionType === "submit" ? "active" : ""}`}
                                handleClick={() => handleSetType("submit")}
                            />
                        </li>
                        <li class="nav-item">
                            <CustomButton
                                text="Draft"
                                classNames={`nav-link px-4 ${submissionType == "draft" ? "active" : ""}`}
                                handleClick={() => setSubmissionType("draft")}
                            />
                        </li>
                    </ul>
                </div>
                <div class="tab-content">
                    <div class="tab-pane fade active show" id="sumittedMrfStatus">
                        <CustomTable
                            data={allMrfListData?.data}
                            fields={mrfSubmittedFields}
                            isLoading={isLoading}
                            pagination={true}
                            currentPage={currentPage}
                            setCurrentPage={setCurrentPage}
                            totalPages={allMrfListData?.totalPages}
                            isHeader={false}
                            tableStyle={{ padding: "0px", boxShadow: "none" }}
                            scopedSlots={scopedSlots}
                        />
                    </div>


                </div>
            </div>
        </div>
    )
}

export default MrfSubmissionTable
